

import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default{
components: {
XHeader,
ButtonTab,
ButtonTabItem,
Group,
XInput,
Cell,
PopupPicker,
Datetime,
XButton
},
data () {
return {
cardList: [['6210 **** **** 1219', '6210 **** **** 3341', '6210 **** **** 2567']],
purposeList: [['还款', '消费', '转账']], // 付款用途
selectTypeList: [['还款']], // 选择种类
intervalTypeList: [['一次', '两次']], // 定时种类
purpose: ['消费'],
selectType: ['还款'],
intervalType: ['一次'],
intervalTypeQuery: [],
tabSelected: 0,
tabShow: '1',
cardNo: ['6210 **** **** 1219'], // 卡号
cardNoQuery: [], // 查询记录是选卡
cardAlias: '银行卡', // 别名
currType: '人民币', // 币种
tranAmt: '1000.00', // 转出金额
uppercaseAmt: '壹仟元', // 大写金额
payeeName: '', // 收款人/
payeeAccno: '', // 收款人账号/
startDate: '2018-05-20',
startDateQuery: '2018-04-20',
endDateQuery: '2018-05-20'
}
},
methods: {
back () {

this.$publicFun.goBack(this)
},
submit (val) {
this.tipsShow = true
},
gotoNext () { // 1活转定 2定转活

this.$router.push({path: 'ReserveRemitSure', query: {cardNo: this.cardNo[0], tranAmt: this.tranAmt, payeeName: this.payeeName, payeeAccno: this.payeeAccno, intervalType: this.intervalType[0], purpose: this.purpose[0], startDate: this.startDate}})
},
gotoQuery () {
console.log('调用查询接口')
},
change (val) {
this.startDate = val
},
changeStart (val) {
this.startDateQuery = val
},
changeEnd (val) {
this.endDateQuery = val
}
}
}



